<template>
  <tas-base-crud ref="crud-cutoff" :config="config" hide-detail hide-create hide-update hide-delete hide-filter>
    <template v-slot:header-table>
      <div class="card card-custom mb-2">
        <div class="card-body py-2 px-4">
          <div class="d-flex justify-content-between">
            <!-- <s-input v-model="config.filter_api.periode_year" :item="{ label:'', nm:false, inline:false, type:'year', prefix:'Tahun Pengaturan Cut Off', validation:['required'] }" /> -->
            <s-input v-model="tempYear" :item="{ label:'', nm:false, inline:false, type:'year', prefix:'Tahun Pengaturan Cut Off', validation:['required'] }" />
            <button @click="openModalDate({}, 'global')" class="btn btn-primary">
              <i class="ri-timer-line"></i> Atur Cut Off Global </button>
          </div>
        </div>
      </div>
      <b-modal id="dateModals" @hide="resetDate()" centered hide-footer hide-header size="sm">
        <div class="text-center">
          <span class="font-size- font-weight-bold">Cut Off Tahun {{ tempYear | parse('year') }}<br>{{ tempDate.name }} </span>
          <hr class="mt-1">
          <s-input v-model="tempDate.date" :item="{ label:'', nm:true, inline:true, type:'datetime', validation:['required'] }" />
          <div class="d-flex">
            <button @click="saveModals()" class="btn btn-block btn-sm btn-primary mt-3">Simpan</button>
            <!-- <button @click="tempDate.date = null;saveModals()" class="btn btn-icon btn-sm btn-outline-danger mt-3 ml-3 px-4">
              <i class="ri-delete-bin-2-line"></i>
            </button> -->
          </div>
        </div>
      </b-modal>
    </template>
    <template v-slot:list-division_name="rowData">
      <span v-if="rowData" class="switch switch-outline switch-icon switch-primary">
        <label class="d-flex align-items-center">
          <div>
            <input @change="saveModals(rowData.rowData)" type="checkbox" v-model="rowData.rowData.active" :true-value="1" :false-value="0">
            <span></span>
          </div>
          <div class="ml-3">{{rowData.rowData.division_name }} ({{rowData.rowData.division_code }})</div>
        </label>
      </span>
    </template>
    <template v-slot:list-closing_date="rowData">
      <div v-if="rowData" class="d-flex align-items-center">
        <span class="font-weight-bold">{{ rowData.rowData.closing_date || 'Belum ditentukan' | parse('longDateTimeUTC') }}</span>
        <button @click="openModalDate(rowData.rowData, 'single')" class="btn btn-icon btn-sm btn-light-primary ml-3">
          <i class="ri-edit-2-line"></i>
        </button>
      </div>
    </template>
  </tas-base-crud>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'crud-cut-off-qualitatif-schedules',
  data () {
    return {
      config: {
        title: 'Pengaturan Cut Off',
        model_api: '',
        getter: 'cut-off-qualitatif-schedules',
        setter: 'cut-off-qualitatif-schedules',
        pk_field: 'name',
        custom_api: {
          list: 'custom/cut-off-qualitatif-schedules/list'
        },
        filter_api: {
          periode_year: moment().set('date', 1).set('month', 0).format('YYYY-MM-DD'),
          sort_by: 'id'
        },
        permission: {
          create: 'create-cut-off-qualitatif-schedules',
          read: 'view-cut-off-qualitatif-schedules',
          update: 'update-cut-off-qualitatif-schedules',
          delete: 'delete-cut-off-qualitatif-schedules'
        },
        fields: [
          {
            id: 'division_name',
            label: 'Nama Divisi',
            methods: {
              list: { type: 'text' }
            }
          },
          {
            id: 'bu_type',
            label: 'Tipe',
            methods: {
              list: { type: 'text', class: 'text-uppercase', transform: 'capitalize' }
            }
          },
          {
            id: 'closing_date',
            label: 'Waktu Closing',
            methods: {
              list: { type: 'row-slot' }
            }
          }
          // {
          //   id: 'rel_updated_by',
          //   label: 'Update Terakhir',
          //   methods: {
          //     list: { type: 'text' }
          //   }
          // }
        ]
      },
      tempDate: {
        type: null,
        date: moment().format('YYYY-MM-DD'),
        name: '',
        id: 0
      },
      tempYear: moment().set('date', 1).set('month', 0).format('YYYY-MM-DD')
    }
  },
  watch: {
    tempYear: {
      deep: false,
      immediate: false,
      handler (e) {
        this.reloadList({ periode_year: moment(e).set('date', 1).set('month', 0).format('YYYY-MM-DD'), sort_by: 'id' })
      }
    }
  },
  methods: {
    openModalDate (value = {}, type) {
      this.tempDate = Object.assign({}, { date: value.closing_date || moment().format('YYYY-MM-DD'), type: type, name: value.division_name || 'Global', id: value.id || 0 })
      this.$bvModal.show('dateModals')
    },
    resetDate () {
      this.tempDate = Object.assign({}, { date: moment().format('YYYY-MM-DD'), type: null })
    },
    saveModals (data = null) {
      if (!data) {
        const api = this.tempDate.type === 'global' ? 'custom/cut-off-qualitatif-schedules/update-all' : 'custom/cut-off-qualitatif-schedules/update'
        let form = {
          periode_year: this.tempYear,
          closing_date: this.tempDate.date
        }
        if (this.tempDate.type === 'single') form.division_id = this.tempDate.id
        this.$_api.put(api, form).then(res => {
          this.$_alert.success('Cut Off berhasil diperbarui')
          this.$bvModal.hide('dateModals')
          this.reloadList()
        }, err => {
          this.$_alert.error(err)
        })
      } else {
        data.periode_year = this.tempYear
        data.closing_date = data.closing_date || moment().format('YYYY-MM-DD')
        data.division_id = data.id
        this.$_api.put('custom/cut-off-qualitatif-schedules/update', data).then(res => {
          this.$_alert.success('Cut Off berhasil diperbarui')
          this.reloadList()
        }, err => {
          this.$_alert.error(err)
        })
      }
    },
    reloadList (setFilter = null) {
      if (!setFilter) this.$refs['crud-cutoff'].$children[1].getData()
      else this.$set(this.$refs['crud-cutoff'].$children[1]['paramAPI'], 'filters', setFilter)
    }
  }
}
</script>



